.container {
	max-width: 120rem;
	margin: 8rem auto;
	background-color: var(--grey-light-1);
	box-shadow: var(--shadow-dark);
	min-height: 50rem;

}

.header{
	height: 7rem;
	background-color: #fff;
	border-bottom: var(--grey-light-2);
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 1.4rem;
	
}

.content{
	display: flex;
}

.sidebar{
	background-color: var(--grey-dark-1);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex: 0 0 18%; // grow, shrink, basis(width)
}

.hotel-view{
	background-color: #fff;	
	flex: 1; // will grow and claim whole remaining space
	height: 80rem;
}